<template>
  <v-app id="usuario-crud">
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md10>
            <h1 class="text-center text-color pb-5">GESTÃO USUÁRIO</h1>
            <v-card class="pa-1" color="#C0C0C0">
              <v-card-text>
                <v-data-table
                  :headers="headers"
                  :items="lista_de_usuarios"
                  sort-by="calories"
                  class="elevation-1"
                >
                  <template v-slot:top>
                    <v-toolbar flat>
                      <v-toolbar-title>Gestão de Usuário</v-toolbar-title>
                      <v-divider class="mx-4" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            color="primary"
                            dark
                            class="mb-2"
                            v-bind="attrs"
                            v-on="on"
                          >
                            Novo Usuário
                          </v-btn>
                        </template>
                        <v-card color="white">
                          <v-card-title>
                            <span class="text-h5 text-color">{{
                              formTitle
                            }}</span>
                          </v-card-title>
                          <v-card-text>
                            <v-container>
                              <v-form
                                ref="form"
                                v-model="valid"
                                lazy-validation
                                @submit="cadastrar_usuario"
                              >
                                <v-row justify="center">
                                  <v-col cols="24">
                                    <span
                                      style="color: #274c77; font-size: 18px"
                                      >Nome</span
                                    >
                                    <v-text-field
                                      label="Nome"
                                      v-model="usuario.nome"
                                      :rules="regra_nome"
                                      single-line
                                      solo
                                      required
                                      dense
                                      background-color="#e0e1dd"
                                    ></v-text-field>
                                  </v-col>
                                </v-row>
                                <v-row justify="center">
                                  <v-col cols="24">
                                    <span
                                      style="color: #274c77; font-size: 18px"
                                      >E-mail</span
                                    >
                                    <v-text-field
                                      label="E-mail"
                                      v-model="usuario.email"
                                      :rules="regra_email"
                                      single-line
                                      solo
                                      required
                                      dense
                                      background-color="#e0e1dd"
                                    ></v-text-field>
                                  </v-col>
                                </v-row>
                                <v-row justify="center">
                                  <v-col cols="24">
                                    <span
                                      style="color: #274c77; font-size: 18px"
                                      >Senha</span
                                    >
                                    <v-text-field
                                      label="Senha"
                                      v-model="usuario.senha"
                                      :rules="regra_senha"
                                      background-color="#e0e1dd"
                                      single-line
                                      solo
                                      required
                                      dense
                                      password
                                      :append-icon="
                                        show1 ? 'mdi-eye' : 'mdi-eye-off'
                                      "
                                      :type="show1 ? 'text' : 'password'"
                                      @click:append="show1 = !show1"
                                    ></v-text-field>
                                  </v-col>
                                </v-row>
                                <v-row justify="center">
                                  <v-col cols="24">
                                    <span
                                      style="color: #274c77; font-size: 18px"
                                      >Tipo de Acesso</span
                                    >
                                    <v-select
                                      :items="tipo_acesso"
                                      label="Tipo de Acesso"
                                      v-model="usuario.tipoAcesso"
                                      single-line
                                      solo
                                      required
                                      dense
                                      background-color="#e0e1dd"
                                      :rules="[
                                        (v) =>
                                          !!v ||
                                          'O tipo do usuário é obrigatório',
                                      ]"
                                    ></v-select>
                                  </v-col>
                                </v-row>
                                <v-row>
                                  <v-col></v-col>
                                  <v-col>
                                    <v-btn text color="#274c77" @click="close">
                                      Cancelar
                                    </v-btn>
                                  </v-col>
                                  <v-col>
                                    <v-btn
                                      color="#1b263b"
                                      class="white--text mr-4"
                                      type="submit"
                                      :disabled="!valid"
                                    >
                                      Salvar
                                    </v-btn>
                                  </v-col>
                                </v-row>
                              </v-form>
                            </v-container>
                          </v-card-text>
                        </v-card>
                      </v-dialog>
                      <v-dialog v-model="dialogDelete" max-width="540px">
                        <v-card color="white">
                          <v-card-title class="text-h5 text-color"
                            >Tem certeza de que deseja excluir este
                            item?</v-card-title
                          >
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn text color="#274c77" @click="closeDelete">
                              Cancelar
                            </v-btn>
                            <v-btn
                              color="#C84634"
                              class="white--text mr-4"
                              @click="deletar_usuario(usuario)"
                              >Sim</v-btn
                            >
                            <v-spacer></v-spacer>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-toolbar>
                  </template>
                  <!-- <template v-slot:item.actions="{ item }"> -->
                  <template v-slot:[`item.actions`]="{ item }">
                    <v-icon class="mr-2" @click="editar_usuario(item)">
                      mdi-pencil
                    </v-icon>
                    <v-icon @click="deleteItem(item)"> mdi-delete </v-icon>
                  </template>
                </v-data-table>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Usuario from "../services/usuario";
import Swal from "sweetalert2";

export default {
  name: "usuario-crud",
  data: () => ({
    // Criando o objeto que vai ser feito o POST
    usuario: {
      id: "",
      nome: "",
      email: "",
      senha: "",
      tipoAcesso: "",
    },

    // Validando se os campos do formulario estão preenchidos e se são validos
    valid: true,
    regra_nome: [(v) => !!v || "O nome é obrigatório"],
    regra_email: [
      (v) => !!v || "O e-mail é obrigatório",
      (v) => /.+@.+\..+/.test(v) || "E-mail inválido",
    ],
    regra_senha: [(v) => !!v || "A senha é obrigatória"],

    // Criando os arrays que vão armazenar os conteudos dos selects de Status do Usuario e Tipo de Usuario
    tipo_acesso: ["ADMINISTRADOR", "VENDEDOR", "INTELIGENCIA"],

    // Criando a variavel pro icone de mostrar a senha
    show1: false,

    // Array aonde vai ser armazenado a lista de usuarios
    lista_de_usuarios: [],

    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "ID",
        align: "start",
        value: "id",
      },
      { text: "Nome", value: "nome" },
      { text: "Email", value: "email" },
      { text: "Tipo acesso", value: "tipoAcesso" },
      { text: "Actions", value: "actions", sortable: false },
    ],
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Novo Usuário" : "Editar Usuário";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  mounted() {
    // Chamando o método exibir_usuario()
    this.exibir_usuario();
  },
  methods: {
    // Método de cadastro de usuario
    cadastrar_usuario() {
      // Se o usuario não tiver um "id" significa que esse usuario não existe então ele vai pra resquest de cadastro
      if (!this.usuario.id) {
        Usuario.salvar_usuario(this.usuario)
          .then((resposta_cadastro_usuario) => {
            this.usuario = {};
            Swal.fire(
              "Sucesso",
              "Usuário " +
                resposta_cadastro_usuario.data.nome +
                " cadastrado com sucesso!!!",
              "success"
            );
            this.exibir_usuario();
          })
          .catch((e) => {
            Swal.fire(
              "Oops...",
              "Erro ao cadastrar o usuário! - Erro: " + e.response.data.error,
              "error"
            );
          });
        this.close();
      } else {
        // Método de atualizar usuario
        // Se o usuario já tiver um "cod" ele já existe então ele vai pra request de atualizar
        Usuario.atualizar_usuario(this.usuario)
          .then((resposta_atualizar_usuario) => {
            this.usuario = {};
            Swal.fire(
              "Sucesso",
              "Usuário " +
                resposta_atualizar_usuario.data.nome +
                " atualizado com sucesso!!!",
              "success"
            );
            this.exibir_usuario();
          })
          .catch((e) => {
            Swal.fire(
              "Oops...",
              "Erro ao atualizar o usuário! - Erro: " + e.response.data.error,
              "error"
            );
          });
        this.close();
      }
    },
    // Método pra exibir os usuarios
    exibir_usuario() {
      Usuario.listar_usuarios()
        .then((resposta_lista_usuarios) => {
          this.lista_de_usuarios = resposta_lista_usuarios.data;
        })
        .catch((e) => {
          Swal.fire(
            "Oops...",
            "Erro ao carregar a tabela de usuários! - Erro: " +
              e.response.data.error,
            "error"
          );
        });
    },

    // Método que valida se os campos estão preenchidos, se não estiverem ele bloqueia o botão CADASTRAR
    validate() {
      this.$refs.form.validate();
    },

    // Método que vai recuparar os dados da tabela e armazenar no objeto usuario
    editar_usuario(usuario) {
      this.editedIndex = this.lista_de_usuarios.indexOf(usuario);
      this.usuario = Object.assign({}, usuario);
      this.dialog = true;
    },

    // Método que vai recuparar os dados da tabela e armazenar no objeto usuario
    deleteItem(usuario) {
      this.editedIndex = this.lista_de_usuarios.indexOf(usuario);
      this.usuario = Object.assign({}, usuario);
      console.log(this.usuario);
      this.dialogDelete = true;
    },

    // Método pra excluir os usuarios
    deletar_usuario(usuario) {
      Usuario.excluir_usuario(usuario)
        .then((resposta_excluir_usuario) => {
          Swal.fire("Sucesso", "Usuário excluido com sucesso!!!", "success");
          resposta_excluir_usuario;
          this.exibir_usuario();
        })
        .catch((e) => {
          Swal.fire(
            "Oops...",
            "Erro ao excluir o usuário! - Erro: " + e.response.data.error,
            "error"
          );
        });
      this.closeDelete();
    },

    // Método que vai fechar o modal "dialog"
    close() {
      this.dialog = false;
      this.usuario = {};
    },

    // Método que vai fechar o modal "dialogDelete"
    closeDelete() {
      this.dialogDelete = false;
      this.usuario = {};
    },
  },
};
</script>

<style>
#usuario-crud {
  background-color: white;
}
.text-color {
  color: #274c77;
}
</style>
